p.scrollValue{
    display: none;
    font-size:30px;
    position: fixed;
    z-index: 40;
    left:5px;
    top:5px;
}

@font-face {
    font-family: Hakubo;
    src: url('../HakuboFreeTrial-7Bo7A.otf') format('opentype');
}
div.sideMenu{
    position: fixed;
    top:0;
    transition: left 0.6s ease;
    z-index:2000;
    background:rgba(0,0,0,0.8);
    height:100%;
    width:100vw;
    text-align: center;
    *{
        color:white !important;
    }
    div.user{
        margin-top:20px;
        *{
            margin:10px;
        }
        img{
            width:20%;
        }
        p.name{
            font-size:3vw;
        }
        p.location{
            position: relative;
            top: -3px;
            font-size:2vw;
        }
    }
    div.close{
        width:fit-content;
        padding:4px;
        border-radius: 7px;
        transition: all 0.2s ease;
        position: absolute;
        left:10px;
        top:10px;
        img{
            width:20px;
        }
        &:hover{
            cursor:pointer;
        }
    }
    div.sideMenuLinks{
        a{
            text-decoration: none;
            color:white;
            width:100%;
            padding:20px;
            display: block;
            transition: all 0.2s ease;
        }
        a:hover{
            color:black;
            background: white;
        }
    }
}
div.Home > div.myContainer{
    color: white;
    // width: 100vw;
    height: 550px;
    background: url('../bw.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    p{
        color:white;
    }
}
nav{
    padding: 22px;
    width:96vw;
    position: relative;
    a{
        text-decoration: none;
        color: white;
    }
    a:hover{
        color:white;
    }
    a.brand{
        font-size: 30px;
        position: relative;
        top: -4px;
    }
}
div.hamburger{
    border: 1px solid rgb(32, 32, 32);
    width:fit-content;
    padding:4px;
    border-radius: 7px;
    transition: all 0.2s ease;
    display: none;
    &:hover{
        background:rgba(255,255,255,0.15);
        cursor:pointer;
    }
}
div.links{
    display: inline-block;
    position: absolute;
    right: 5vw;
    font-size: 20px;
    a{
        margin:20px;
    }
    a:hover{
        opacity: 0.5;
    }
    *{
        transition: all 0.2s ease;
    }
}
a.brand{
    font-family: Hakubo;
}
div.jumbshoes{
    position: relative;
    padding:20px;
    width: 96vw !important;
    height:450px;
    top:50px;
    display: flex;
    margin:auto;
    div.info{
        margin-bottom: 30px;
    }
    div.shoe{
        text-align: center;
        img{
            position: relative;
        }
    }
    & > div{
        width: 50%;
    }
}
@media screen and (min-width: 768px) {
    p.type{
        font-size:4vw;
    }
    p.name{
        font-size:3vw;
    }
    div.links{
        font-size: 2vw;
    }
    div.jumbshoes img{
        width:43vw;
    }
    div.sideMenu{
        display: none;
    }
}
@media screen and (max-width: 768px) {
    div.Home > div.myContainer{
        height: 700px;
        text-align: center;
    }
    div.jumbshoes{
        display: block;
        padding:10px;
        top:0;
        & > div{
            width: 100%;
        }
        img{
            width: 60vw;
        }
    }
    div.hamburger{
        position: absolute;
        right:-15px;
        display:inline-block;
    }
    a.brand{
        font-size:40px !important;
    }
    div.links{
        display: none;
        font-size: 2vw;
    }
    div.info{
        text-align: center;
    }
    p.type{
        font-size:7vw;
    }
    p.name{
        font-size:6vw;
        margin-bottom:20px;
    }
    p.description{
        font-size:14px;
    }
}
@media screen and (max-width:520px) {
    nav{
        width:91vw !important;
    }
    div.Home{
        p.type{
            font-size:10vw !important;
        }
    }
    div.sideMenu{
        p.name{
            font-size:4vw !important;
        }
        p.location{
            font-size:2.4vw !important;
        }
        a{
            font-size:3vw;
        }
        div.user img{
            width:40% !important;
        }
    }
    div.jumbshoes {
        width:92vw !important;
        padding:0 !important;
        img{            
            width:90vw !important;
        }
    }
    p.description{
        font-size:13px;
    }
}
@media screen and (max-width: 400px) {
    nav{
        a.brand{
            text-align: center;
        }
    }
    div.sideMenu a {
        font-size:3.6vw;
    }
}