div.Categories{
    div.main{
        width:92vw;
        margin:auto;
    }
    div.grid{
        display: grid;
        margin: auto;
        row-gap:13px;
        width: 77vw;
        a{
            position: relative;
            transition: all 0.3s ease;
            display: block;
        }
        a:hover{
            transform: scale(1.03, 1.03);
        }
        a p{
            position: absolute;
            font-size:3.2vw;
            z-index:10;
            color: black;
            padding:15px;
            bottom:0;
            width:100%;
        }
    }
    a.womens{
        width:40vw;
        height: fit-content;
    }
    a.kids, a.mens{
        width: 34.5vw;
    }
    a img {
        width:100%;
    }
    a.womens{
        grid-column: 1 / 2;
        grid-row:1 / 3;
    }
    a.kids{
        grid-column: 2 / 3;
        grid-row: 1 / 2;
    }
    a.mens{
        grid-column: 2 / 3;        
        grid-row: 2 / 3;
    }
    @media screen and (max-width:768px) {
        div.grid{
            display: block;
            margin: auto;
            a{
                width:70vw;
                margin:20px auto;
            }
            p{
                font-size:40px !important;
                text-align: center;
                font-weight: 600;
            }
        }    
    }
    @media screen and (max-width:500px) {
        div.grid p{
            font-size:9vw !important;
        }
    }
}