a.scrollToTop{
    border-radius: 100%;
    padding:10px;
    position: fixed;
    bottom:20px;
    right:10px;
    z-index:100;
    transform: rotateZ(90deg);
    opacity:0;
    transition: all 0.2s ease;
    img{
        transition: all 0.2s ease;
        width:30px;
        border-radius: 100%;
    }
    &:hover img{
        width:32px;
    }
}
a:hover{
    color:black;
}
p.reason{
    margin-top:100px;
    font-size:55px;
    font-weight: 700;
    text-align: center;
    letter-spacing: -2px;
}
p.reasonBelow{
    font-size:20px;
    text-align: center;
    width:55vw;
    margin:auto;
    margin-bottom: 100px;
}
p.header{
    font-size:60px;
    letter-spacing: -2px;
    font-weight: 700;
}
div#Footer{
    background-color: black;
    text-align: center;
    font-size: 30px;
    margin-top: 20px;
    padding: 5px;
    p{
        color:white;
        margin: 0;
    }
}
@media screen and (max-width: 768px){
    p.reason{
        font-size:6.3vw !important;
    }
    p.header{
        font-size:50px !important;
    }
    p.reasonBelow{
        font-size:17px;
    }
}
@media screen and (max-width:550px) {
    p.header{
        text-align: center;
    }
}
@media screen and (max-width: 450px) {
    p.reason{
        font-size:8vw !important;
    }
    p.reasonBelow{
        font-size:15px !important;
    }
}