div.TrendingNow{
    position: relative;
    padding:40px;
    transition: all 1.6s ease;
    margin-bottom:50px;
    a.Button{
        position: relative;
        top:20px;
    }
    div.nikeShoe{
        opacity: 0;
        transition: all 1s ease;
        position: relative;
        text-align: center;
        margin-top:50px;
        img{
            width:46vw;
        }
        div.logo{
            position: sticky;
            top:10px;
            z-index: 40;
            left:10px;
        }
    }
    p.head{
        font-size:70px;
    }
    p.heading{
        font-size:40px;
    }
    div.line{
        position: relative;
        width:90vw;
        height:6px;
        background:black;
    }
    div.flex{
        display: flex;
        padding:20px;
        position: relative;
        img{
            overflow: visible;
        }
    }
    div.info{
        text-align: center;
    }
    @media screen and (max-width: 780px) {
        p.header{
            font-size:50px !important;
        }
        p.head{
            font-size:50px !important;
        }
        div.flex{
            display: block !important;
            img{
                width:65vw !important;
            }
        }
    }
    @media screen and (max-width:550px) {
        p.header{
            text-align: center;
        }
        p.heading{
            text-align: center;
            font-size:8vw !important;
        }
        div.info{
            font-size:4vw;
        }
        div.line{
            display: none;
        }
        p.head{
            font-size:10vw !important;
        }
        div.TrendingNow div.flex{
            overflow: visible !important;
            padding:0 !important;
            img{
                width: 85vw !important;
            }
        } 
    }
}