.Button{
    // transform: scale(0.5, 0.5);
    text-decoration: none;
    border-width: 6px;
    border-style: solid;
    padding: 15px;
    width:320px;
    margin:auto;
    position: relative;
    transition: all 0.1s ease-out;
    font-size: 40px;
    *{
        text-decoration: none;
    }
    &:hover{
        color:black;
        cursor:pointer;
        box-shadow: 0px 0px 0px 3px;
    }
    @media screen and (max-width:400px) {
        font-size:8vw;
        width:70vw;
    }
    @media screen and(max-width:320px){
        padding:10px;
        font-size:20px;
        border-width: 4px;
    }
    @media screen and (max-width:240px) {
        font-size:15px;
        border-width: 2px;
        &:hover{
            box-shadow: 0px 0px 0px 1px;
        }    
    }
}