div.Jordan{
    position: relative;
    text-align: center;
    transition: all 1s ease;
    div.Top{
        opacity: 0;
        display:grid;
        padding:0;
        div{
            grid-area: 1 / 1;
        }
    }
    div.cover img{
        width:100%;
    }
    svg{
        position: sticky;
        top:15px;
        z-index:40;
    }
    div.content{
        margin-top:10px;
        position: relative;
        z-index: 30;
        p.top{
            text-align: center;
            letter-spacing: 15px;
            margin-top:40px;
            font-size:3.5vw;
            color:whitesmoke;
        }
        div.shopNow{
            position: absolute;
            bottom:70px;
            width:100%;
        }
        a.Button{
            display:block;
            width:300px;
        }
    }
    div.categ{
        width:95vw;
        margin:50px auto;
        display:flex;
        justify-content: space-around;
        div{
            transition: all 0.2s ease-out;
            width:37vw;
        }
        a{
            position: relative;
        }
        h3{
            position: relative;
            z-index:10;
            color:white;
            top:-60px;
        }
        div:hover{
            a{
                color:black;
            }
            transform: scale(1.05, 1.05);
        }
        img{
            width:100%;
        }
    }
    @media screen and (max-width: 768px){
        div.categ{
            display:block;
            div{
                margin:50px auto;
                width:70vw;
            }
        }
        a.Button{
            transform: scale(0.8, 0.8);
        }
        p.top{
            letter-spacing: 10px !important;
        }
        div.shopNow{
            text-align: center !important;
            bottom:40px !important;
        }
        svg{
            width:80px;
            height:76px;
        }
    }
    
    @media screen and (max-width: 650px) {
        p.top{
            font-size:6vw !important;
            padding:0 12px;
        }        
    }
    @media screen and (max-width: 450px) {
        p.top{
            font-size:7.5vw !important;
        }
        a.Button{
            width:70vw !important;
        }
        div.categ h3{
            font-size: 6vw !important;
        }
    }
}
