div.ultraBoost{
    // *{
    //     border:1px solid red;
    // }
    position: relative;
    opacity: 0;
    width:97vw;
    margin:auto;
    transition: all 1s ease;
    text-align: center;
    img.adidasLogo{
        position: sticky;
        width:100px;
        top:10px;
        z-index:20;
    }
    p.reason{
        letter-spacing: 0;
    }
    img{
        width:100%;
    }
    div.heading{
        position: relative;
        text-align: center;
        font-size:7vw;
        margin-bottom:20px;
    }
    div.lineColored{
        margin:auto;
        display: flex;
        width:45vw;
        height:7px;
        div{
            height:100%;
        }
    }
    div.one{
        width:20%;
        background:#F2FE38;
    }
    div.two{
        width:35%;
        background:#D1FB7D;
    }
    div.three{
        width:20%;
        background:#FA5AD1;
    }
    div.four{
        width:25%;
        background:#F0FD3B;
    }
    p.two{
        margin:20px;
        width: fit-content;
        font-size:30px;
        border-bottom:2px solid black;
    }
    div.myContainer{
        position: relative;
        top:100px;
        transition:all 1s ease;
        opacity:0;
        display:flex;
        flex-flow:row wrap;
        justify-content: center;
        align-content: space-between;
    }
    div.shoe{
        margin:10px;
        width:21vw;
        position: relative;
        transition:all 0.3s ease;
        border-radius: 5px;
        img{
            width:100%;
        }
    }
    div.shoe:hover{
        z-index:10;
        box-shadow: 0 0 20px rgba(0,0,0,0.2);
        transform: scale(1.06, 1.06);
        cursor: pointer;
    }
    div.info{
        padding:5px;
    }
    div.line{
        width:80%;
        margin:auto;
        background:black;
        height:4px;
    }
    div.info div{
        width:100%;
        display: flex;
        flex-flow:row nowrap;
        justify-content: space-between;
    }
    p.price{
        font-size:15px;
    }
    p.extraDetail{
        color:#FA5400;
        font-size:13px;
    }
    p.name{
        font-size:1.2vw;
    }
}
@media screen and (max-width:768px) {
    div.ultraBoost{
        width:95vw;
        div.heading{
            font-size:50px !important;
        }
        div.line, div.lineColored{
            height:4px !important;
        }
        p.name{
            font-size:12px !important;
        }
        p.two{
            font-size:30px;
        }
        div.shoe{
            width:30vw !important;
            margin:0 !important;
        }
        p.extraDetail{
            font-size:15px;
        }
    }
}

@media screen and (max-width:600px){
    div.ultraBoost{
        div.heading{
            font-size:40px !important; 
        }
        div.lineColored{
            width:220px !important;
        }
        div.shoe{
            width:46vw !important;
        }
        p.name{
            font-size:2.7vw !important;
        }
        p.extraDetail{
            font-size:2.5vw !important;
        }
        div.line{
            height: 4px !important;
        }    
    }
}

@media screen and (max-width:400px) {
    div.ultraBoost{
        width:93vw !important;
        p.reason{
            font-size:40px !important;
        }
        div.heading{
            font-size:30px !important;
        }
        div.shoe{
            width: 80vw !important;
        }
        p.name{
            font-size:15px !important;
        }
        p.extraDetail{
            font-size:12px !important;
        }
    }
}